<template>
    <div style="" class="m2item">

        <span v-html="replaced"></span>
        <ol type="a">
            <li v-for="(op, index) in options" class="py-1">
                <!--<button @click="click(index)">{{op}}</button>-->
                <label class="cursor-pointer">
                    <input :name="nameRadio" type="radio" class="inputRadio"/>
                    <span class="spanOption" @click="click(index)">{{ concatenate(op)}}</span>
                </label>
            </li>
        </ol>
    </div>
</template>
<script>
    /* eslint-disable */
    import Vue from 'vue'
//    import "materialize-css/dist/css/materialize.css"
//    import "materialize-css/dist/js/materialize.min"

    export default {
        name: 'M2Item',
        components: {},
        props: {
            options: Array,
            sentence: String,
            nameRadio: String
        },
        data: function () {
            return {
                toReplace: '________',
                newString: '',
                selectedIndex: -1
            }
        },
        computed: {
            replaced(){
                console.log("replaced");
                this.newString = this.sentence;
                if (this.selectedIndex === -1) {
                    this.options[0].forEach(op => this.newString = this.newString.replace("###", this.toReplace));
                } else {
                    this.options[this.selectedIndex].forEach(op => this.newString = this.newString.replace("###", this.getSpan(op)));
                }
                return this.newString;
            }
        },
        methods: {
            click(index){
                this.selectedIndex = index;
            },
            getSpan(str){
                return '<span class="bold">' + str + '</span>'
            },
            concatenate(stringArray){
                let separator = '';
                let concatenated = '';
                stringArray.forEach(str => {
                    concatenated += separator + str;
                    separator = " - ";
                });
                return concatenated;
            }
        },
        mounted: function () {
            console.log("mounted")
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

    .inputRadio{
        display: none;
        visibility: hidden;
    }

    .spanOption:hover{
        background-color: #e7e7e7;
    }

    .inputRadio:checked + .spanOption {
        background: #2BBBAD;
        color: white;

    }

    .spanOption{
        border-radius: .4em;
        padding: 0 .5em 0;
    }

    .m2item /deep/ .bold {
        font-weight: bold;
        color: gray;
        border-bottom: 2px solid #00acc1;
    }



</style>
